<template>
    <div class="login">
        <div class="logo">
            <img src="../assets/logo.png" alt="">
        </div>
        <div class="input-area">
            <div class="user-name">
                <p>用户名/手机号</p>
                <input type="text" v-model="username">
            </div>
            <div class="password">
                <p>密码</p>
                <input type="password" v-model="password">
            </div>
        </div>
        <div class="login-btn">
            <button @click="login">登 录</button>
        </div>
    </div>
</template>

<script>
    import {Toast} from 'mand-mobile'
    export default {
        name: "login",
        data() {
            return {
                username: '',
                password: '',
            }
        },
        methods: {
            login() {
                if (!this.checkFields()) return;
                this.$http.post('/api/login', {
                    name: this.username,
                    password: this.password,
                    phone: this.phone
                }).then(res => {
                    if (res.data.code === 200) {
                        document.cookie = 'token=' + res.data.token;
                        this.$store.commit('login', res.data.data);
                        Toast.succeed('登陆成功');
                        this.$router.push('/record');
                    } else {
                        Toast.failed(res.data.message);
                    }
                })
            },
            checkFields() {
                let flag = true;
                if (!this.username) {
                    Toast.failed('请输入用户名');
                    flag = false;
                }
                if (!this.password) {
                    Toast.failed('请输入密码');
                    flag = false;
                }
                return flag;
            }
        }
    }
</script>

